import {
	getHomeContent,
	productCateList
} from '@/api/homeApi.js'
import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			swiperCurrent: 0,
			carouselList: []
		}
	},
	computed: {
		...mapState(['classifyList'])
	},
	onReady() {
		//获取轮播数据
		getHomeContent().then(res => {
			if (res.code == 200) {
				this.carouselList = res.data.advertiseList;
			}
		})
	},
	methods: {
		...mapMutations(['setClassifyList']),
		//轮播图切换修改自定义指示器
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
		},
		classify(item) {
			this.$router.navigateTo({
				url: `/pages/product/product?id=${item.id}`
			});
		}
	}
}
